.relation {
  background-color: #eff0ed;
  padding-top: 4px;
  padding-bottom: 4px; }
  .relation_wrapper {
    height: 12px;
    background-color: #eff0ed; }
    .relation_wrapper:hover {
      background-color: #eff0ed !important; }
  .relation:first-child {
    padding-top: 12px; }
  .relation:last-child {
    padding-bottom: 12px; }
  .relation td {
    padding: 0; }
  .relation_name {
    text-align: center;
    font-weight: bold;
    margin: auto; }
  .relation_value {
    margin: auto;
    padding: 12px; }
    .relation_value > * {
      height: 16px;
      padding: 8px 0; }



